.restaurants-list {
  --card-width: 348px;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(var(--card-width), 1fr));
  gap: 40px 20px;

  margin: 32px 0 80px;

  @media (max-width: 980px) {
    --card-width: 340px;
  }

  @media (max-width: 768px) {
    row-gap: 20px;

    margin: 20px 0 40px;
  }

  @media (max-width: 450px) {
    --card-width: 280px;
  }
}
